<template>
  <div id="ct">
    <section class="card">
      <header class="card-header">
        <h2 class="card-title">
          <span class="i-rounded bg-danger"><i class="icon-set" /></span>솔루션
        </h2>
        <h2 class="card-title text-pad">
          |
        </h2>
        <h2 class="card-title text-tertiary">
          <a href="userList">사용자</a>
        </h2>
      </header>
      <div class="ct-header">
        <button
          type="button"
          class="btn-filter collapsed d-xl-none"
          data-toggle="collapse"
          data-target="#collapse-filter"
        >
          검색 필터<i class="icon-down" />
        </button>
        <div
          id="collapse-filter"
          class="collapse collapse-filter"
        >
          <div class="filter no-gutters">
            <div class="col">
              <label class="form-control-label">
                <b class="control-label">등록기간</b>
                <input
                  id="datePicker"
                  type="text"
                  class="form-control input-daterange"
                >
              </label>
            </div>
            <div class="col-auto">
              <button
                type="button"
                class="btn btn-primary"
                style="margin-left: 10px;"
                @click="onSubmit"
              >
                <i class="icon-srch" />조회
              </button>
            </div>
          </div>
        </div>
      </div>
      <div class="ct-content">
        <v-app id="app">
          <ag-grid-vue
            style="width: 100%; height:250px;"
            class="flex-grow-1 flex-shrink-1 ag-theme-alpine"
            :column-defs="columnDefs"
            :row-data="rowData"
            :grid-options="gridOptions"
            :get-row-style="getRowStyle"
            :grid-size-changed="gridSizeFit"
            :grid-ready="gridSizeFit"
            @cell-clicked="onCellClicked"
            @gridReady="gridSizeFit"
            @gridSizeChanged="gridSizeFit"
          />
          <Modal
            ref="popup"
            :dialog="isDialog"
            :propsdata="params"
            @close="isDialog=false"
          />
        </v-app>
      </div>
    </section>
  </div>
</template>

<script>
import 'ag-grid-community/dist/styles/ag-grid.css'
import 'ag-grid-community/dist/styles/ag-theme-alpine.css'

/* daterangepicker */
import '@/assets/vendor/daterangepicker/daterangepicker.min.js'
import '@/assets/vendor/daterangepicker/daterangepicker.min.css'
import '@/assets/vendor/daterangepicker/moment.min.js'

import Modal from '@/components/adm/KAADM0401POPForm'
import { getSolList, getSearchSolList } from '@/api/adm/Point.js'
import { AgGridVue } from 'ag-grid-vue'

import { getSolution } from '@/api/log/Login.js'

/* jQuery 사용 */
global.jQuery = require('jquery')
var $ = global.jQuery
window.$ = $

export default {
  name: 'KAADM0401From',
  components: {
    Modal,
    AgGridVue
  },
  data: () => {
    return {
      lists: '',
      userid: '',
      // grid
      columnDefs: null,
      rowData: [],
      gridOptions: null,
      // pop up
      isDialog: false,
      maxDate: new Date(),
      params: '',
      // date picker
      startDate: '',
      endDate: '',
      range: '',
      now: ''
    }
  },
  beforeMount () {
    this.gridOptions = {
      enableColResize: true,
      enableSorting: true,
      enableFilter: true,
      animateRows: false,
      pagination: true,
      paginationPageSize: 10,
      cellClass: 'grid-cell-centered'
    }

    this.columnDefs = [
      { headerName: '솔루션명', field: 'solution', sortable: true, filter: true },
      { headerName: '질문', field: 'question', sortable: false, filter: true },
      { headerName: '답변', field: 'answer', sortable: false, filter: true },
      { headerName: '미해결', field: 'notSuccess', sortable: false, filter: true },
      { headerName: '평가', field: 'scoreCount', sortable: true, filter: true },
      { headerName: '평점', field: 'avgScore', sortable: true, filter: true },
      { headerName: '경과일', field: 'dueDate', sortable: true, filter: true },
      { headerName: '지식포인트', field: 'point', sortable: true, filter: true }
    ]
  },
  created () {
    console.log('main created!!')
    this.time()
    var moment = require('moment')
    moment.locale('ko') // 언어팩 변경
    this.now = moment().format('YYYY-MM-DD HH:mm') // 현재 시간
    this.month = moment().format('MM') // 현재 월
    this.getList()
  },
  mounted () {
    $('#demo').daterangepicker({
      locale: {
        format: 'MM/DD/YYYY',
        separator: ' - ',
        applyLabel: 'Apply',
        cancelLabel: 'Cancel',
        fromLabel: 'From',
        toLabel: 'To',
        customRangeLabel: 'Custom',
        weekLabel: 'W',
        daysOfWeek: [
          'Su',
          'Mo',
          'Tu',
          'We',
          'Th',
          'Fr',
          'Sa'
        ],
        monthNames: [
          'January',
          'February',
          'March',
          'April',
          'May',
          'June',
          'July',
          'August',
          'September',
          'October',
          'November',
          'December'
        ],
        firstDay: 1
      },
      startDate: '12/01/2016',
      endDate: '12/07/2016'
    }, function (start, end, label) {
    })
    $(() => {
      $('#datePicker').daterangepicker({
        timePicker: true,
        timePicker24Hour: true,
        timePickerSeconds: true,
        autoApply: true,
        locale: {
          format: 'YYYY.MM.DD HH:mm:ss',
          separator: ' ~ ',
          daysOfWeek: ['일', '월', '화', '수', '목', '금', '토'],
          monthNames: ['1월', '2월', '3월', '4월', '5월', '6월', '7월', '8월', '9월', '10월', '11월', '12월']
        }
      }, (start, end) => {
        this.startDate = start.format('YYYY-MM-DD')
        this.endDate = end.format('YYYY-MM-DD')
        console.log('this.startDate', this.startDate) // datePicker시작 날짜
        console.log('this.endDate', this.endDate) //  datePicker끝 날짜
      })
    })
  },
  methods: {
    async onSubmit () {
      const a = {
        params: {
          reg_date_start: this.startDate,
          reg_date_end: this.endDate
        }
      }
      const { data } = await getSolList(a)
      this.lists = data
      this.makeData()
    },
    // Ag-Grid를 만들어보자
    async getList () {
      console.log('getList')
      const a = {
        params: {
          reg_date_start: this.startDate,
          reg_date_end: this.endDate
        }
      }

      const data = await getSolList(a)
      console.log('data.data', data.data)
      this.lists = data.data
      console.log('this.lists', this.lists)
      this.makeData()
    },
    makeData () {
      this.rowData = []
      console.log('makeData << SOL >>')
      this.lists.forEach(e => {
        if (e.dueDate === ' 일') {
          const value = {
            solution: e.solution,
            question: e.question,
            answer: e.answer,
            notSuccess: e.notSuccess,
            scoreCount: e.scoreCount,
            avgScore: e.avgScore,
            dueDate: '--' + e.dueDate,
            point: e.point
          }
          this.rowData.push(value)
        } else {
          const value = {
            solution: e.solution,
            question: e.question,
            answer: e.answer,
            notSuccess: e.notSuccess,
            scoreCount: e.scoreCount,
            avgScore: e.avgScore,
            dueDate: e.dueDate,
            point: e.point
          }
          this.rowData.push(value)
        }
      })
    },
    gridSizeFit (params) {
      // 모니터나 브라우저 크기에 따라 반응하여 그리드 컬럼 사이즈를 조정
      if (window.innerWidth > 800) { // 화면 가로가 800 px 이 넘을 경우
        console.log('innerWidth')
        params.api.sizeColumnsToFit() // 가로 스크롤바가 생기지 않도록 컬럼 사이즈를 그리드에 꼭 맞게 조정
      } else {
      // 컬럼의 데이터값이 잘리지 않도록 조정
        const allColumnIds = []
        this.gridOptions.columnApi.getAllColumns().forEach(function (column) {
          allColumnIds.push(column.colId)
        })
        this.gridOptions.columnApi.autoSizeColumns(allColumnIds)
      }
    },
    async onCellClicked (event) { // 그리드 셀 클릭시 이벤트
      // 그리드 셀 클릭시 이벤트
      console.log('<< onCellClicked !! >>')
      if ((event.colDef.field === 'question') || (event.colDef.field === 'answer') || (event.colDef.field === 'notSuccess')) {
        console.log('1 onCellClicked')
        console.log('event', event)
        console.log('제발', event.colDef.field)
        const data = {
          params: {
            solution: event.data.solution,
            type: event.colDef.field,
            reg_date_start: this.startDate,
            reg_date_end: this.endDate
          }
        }
        console.log('main data : ', data)
        const popData = await getSearchSolList(data)
        console.log('main popData : ', popData)
        this.$refs.popup.popupData(popData)
        this.isDialog = true
      }
    },
    getRowStyle: function (param) {
      return { 'text-align': 'center' }
    },
    time () {
      const date = new Date()
      this.now = date.getDate
      console.log(this.now)
    },
    // 공통코드 솔루션 값
    userSolution () {
      getSolution()
        .then((res) => {
          console.log('res=>>', res)
          this.items = res.data
          console.log('solution_id ', this.items)
        })
        .catch(console.error())
    }
  }
}
</script>

<style>
.ag-header-cell-label{
  justify-content: center
}
</style>
